@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

body {
  font-family:"Inter", sans-serif;
  background-color: #0B1517;
  color: white;
}




.heading{
  width: 800px;
  margin: 97px auto 0px;
}

.heading-text {
  font-size: 50px;
  font-weight: 700px;
  text-align: center;

}

span {
  color:#AEA1EA;
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
}
.content-txt {
  width: 500px;
  margin: auto;
  color: rgb(212, 212, 212);
  text-align: center;
}

.content-txt p {
  font-size: 25px;
  font-weight: 400px;
  
}

.image {
  text-align: center;
  margin-top: 30px;
}

img {
  width: 800px;
  height: 370px;
  border-radius: 20px;

}

.buttons {
  text-align: center;
  display: flex;
  flex-direction: center;
  justify-content: center;
  gap: 15px;
  margin-top: 30px;
  cursor: pointer;
}

button {
  width: 170px;
  height: 47px;
  border-radius: 20px;
}

.primary {
  background-color: #AEA1EA;
  color:white;
  font-size: 20px;
}

.secondary {
  color:black;
  font-size: 20px;
}